import React, { Fragment } from 'react'
import {withStyles} from "@material-ui/core/styles"
import GridContainer from 'components/Grid/GridContainer'
import GridItem from 'components/Grid/GridItem'
import {TextField, Input,Typography, Paper, Button, FormControl, InputLabel} from '@material-ui/core'


const style={
    root:{
        margin:100
    },
   
}

class Ask extends React.Component
{
    constructor(props)
    {
        super(props)
        this.state={
            question:"",
            author:"",
            answer:'a'
        }
        this.change=this.change.bind(this)
        this.submit=this.submit.bind(this)
    }
    change(e)
    {
        if(e.target.name==="author")
        {
            this.setState({
                author:e.target.value
            })
        }
        else{
            this.setState({
                question:e.target.value
            })
        }
    }
    render()
    {
        const {classes}=this.props
        return(<div className={classes.root}>
                <GridContainer>
                    <GridItem>
                        <FormControl fullWidth>
                          <InputLabel  variant="headline">
                             Name   
                          </InputLabel>      
                          <Input
                            name="author"
                            onChange={this.change}
                            value={this.state.author}
                          />
                        
                        </FormControl ><br/>
                        <FormControl fullWidth>
                          <Typography  variant="title">
                                  
                          </Typography>      
                          <TextField
                            placeholder="question"
                            multiline
                            rows="10"
                            margin="normal"
                            inputProps={{
                                name:"name",
                                id:"name"
                            }}
                            name="question"
                            onChange={this.change}
                            value={this.state.author}
                          />
                        </FormControl><br/><br/>                      
                            <Button variant="raised" color="primary" align="right">Next</Button>
                    </GridItem>
                </GridContainer>
            </div>
        )
    }
}

export default withStyles(style)(Ask)