import React from 'react'
import {AppBar, Toolbar, Typography, Button} from '@material-ui/core'
import {NavLink} from 'react-router-dom'

const style={
    root:{
        flexGrow:1
    },
    flex:{
        flex:1
    },
    white:{
        color:"white",
        textDecoration:"none"
    }
}

export default props=>
    <AppBar
        style={style.root}
        position="fixed"
    >
        <Toolbar>
            <Typography 
                variant="title"
                color="inherit"
                style={style.flex}
            >
                konsultant.uz
            </Typography>
            <Button 
                color="inherit"    
            >   <NavLink to='/' style={style.white}>
                    Home
                </NavLink>
            </Button>
            <Button 
                color="inherit"  
            >
                <NavLink to='/ask' style={style.white}>
                    Ask
                </NavLink>
            </Button>
            <Button 
                color="inherit"  
            ><NavLink to='/answer' style={style.white}>
            Check Answer
            </NavLink>
            </Button>
        </Toolbar>
    </AppBar>